.catalog {
  display: flex;
  align-self: flex-end;
  grid-row: 3;
  margin-bottom: 40px;
  box-sizing: border-box;
  transform: translateX(0%);
  transition: 0.4s;

  @include onIpad {
    gap: 30px;
  }

  @include onDesktop {
    gap: 20px;
  }
}

.wrapper {
  overflow: hidden;
  margin-bottom: -40px;
}
